<template>
  <section class="swaps-grid-header">
    <div class="swaps-grid-header__filters">
      <SearchInput
        v-model="filters.search"
        hide-shadow
        :placeholder="t('labels.searchDatabase')"
        class="swaps-grid-header__filter swaps-grid-header__filter-search"
      />
      <DateRange
        v-model="filters.dateRange"
        :placeholder-from="t('common.from')"
        :placeholder-to="t('common.to')"
        class="swaps-grid-header__filter swaps-grid-header__filter-date-range"
      />
      <SelectCoinPair />
    </div>
    <div class="swaps-grid-header__settings">
      <RadioButtons
        v-model="filters.status"
        :data="radioButtons"
      >
        <template #default="{ item }">
          {{ t('swaps.table.filters.' + item.key) }}
        </template>
      </RadioButtons>
      <div class="d-flex align-items-center">
        <SelectSort v-model="filters.sort"/>
        <SelectShowOnPage v-model="perPage"/>
        <SelectColumns :columns="columns" />
      </div>
    </div>
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed, reactive } from 'vue'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import DateRange from '@/shared/components/UIKit/Date/DateRange'
import SelectSort from '@/admin/components/Table/Filters/SelectSort'
import SearchInput from '@/admin/components/Inputs/SearchInput'
import RadioButtons from '@/shared/components/UIKit/RadioButtons'
import SelectColumns from '@/admin/components/Table/Filters/SelectColumns'
import SelectCoinPair from '@/shared/components/UIKit/Select/SelectCoinPair'
import SelectShowOnPage from '@/admin/components/Table/Filters/SelectShowOnPage'

export default {
  name: 'SwapsGridHeader',
  components: {
    DateRange,
    SelectSort,
    SearchInput,
    RadioButtons,
    SelectColumns,
    SelectCoinPair,
    SelectShowOnPage
  },
  setup () {
    const { t } = useI18n()
    const {
      getColumns: columns,
      getFilters: filters,
      getPerPage
    } = useGetters(MODULES_NAMES.SWAPS)

    const {
      SET_PER_PAGE
    } = useMutations(MODULES_NAMES.SWAPS)

    const radioButtons = reactive([
      { key: 'all', title: 'All' },
      { key: 'complete', title: 'Complete' },
      { key: 'progress', title: 'In Progress' },
      { key: 'hold', title: 'On Hold' },
      { key: 'expired', title: 'Expired' }
    ])

    const perPage = computed({
      get: () => getPerPage.value,
      set: SET_PER_PAGE
    })

    return {
      t,
      filters,
      columns,
      perPage,
      radioButtons
    }
  }
}
</script>

