<template>
  <div class="pagination">
    <Button class="pagination__page-next">
      {{ t('pagination.nextPage') }} <Icon name="arrow-right-full" />
    </Button>
    <div class="pagination__right justify-content-between">
      <div class="d-flex justify-end">
        <div class="pagination__pages">
          <div
            v-for="item in pageSlots"
            :key="item"
            class="pagination__pages-item"
          >
            {{ item }}
          </div>
          <div class="pagination__pages-item pagination__page-divider">
            ...
          </div>
          <div class="pagination__pages-item pagination__page-last">
            {{ pagesCount }}
          </div>
        </div>
      </div>
      <div class="d-flex justify-end">
        <div class="pagination__goto">
          <Input
            :placeholder="t('pagination.enterPage')"
            type="number"
            hide-shadow
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import Icon from '@/shared/components/Icon/Icon'
import Button from '@/shared/components/UIKit/Button'
import Input from '@/shared/components/UIKit/Input'
import { useI18n } from '@/shared/plugins/i18n'

export default {
  name: 'Pagination',
  components: { Input, Button, Icon },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Number,
      default: 1
    },
    perPage: {
      type: Number,
      default: 100
    },
    totalCount: {
      type: Number,
      default: 100
    },
    slotsCount: {
      type: Number,
      default: 6
    }
  },
  setup (props) {
    const { t } = useI18n()
    const pagesCount = computed(() => Math.ceil(props.totalCount / props.perPage))
    const pageSlots = computed(() => {
      const arr = []
      for (let i = 0; i < props.slotsCount; i++) {
        arr.push(props.modelValue + i)
      }
      return arr
    })
    return {
      t,
      pagesCount,
      pageSlots
    }
  }
}
</script>

