<template>
  <div v-click-outside="hideInput" class="select-coin-pair">
    <SelectTitle
      :active="!!isSelected"
      class="select-coin-pair__header"
      @click="showInput"
      @clear="clearInput"
    >
      <template v-if="!isSelected">
        <Text
          mr="13px"
        >{{ t('labels.selectPairs') }}</Text>
        <Icon name="arrow-down" />
      </template>
      <Text v-else>{{ t('labels.changePairs') }}</Text>
    </SelectTitle>
    <Container v-show="isShowInput" ref="dropdown" background-color="white" class="select-coin-pair__body">
      <div
        v-for="item in Object.keys(options)"
        :key="item"
        :class="'select-coin-pair__' + item"
      >
        <div
          class="select-coin-pair__crypto-row"
          :class="{ 'select-coin-pair__crypto-row_disabled': options[item].disabled }"
        >
          <Text
            variant="p"
            type="uppercase"
            opacity="0.4"
            :ml="item === 'any' ? '10px' : '20px'"
            mb="10px"
            class="select-coin-pair__label"
          >
            {{ t(options[item].label) }}
          </Text>
          <CheckButton
            v-for="coin in coins"
            :key="coin.key"
            :model-value="isCoinSelected(coin, item)"
            @update:modelValue="selectCoin(coin, item)"
          >
            <Icon width="28px" height="28px" :name="coin.icon + '-circle'" />
            <Text
              type="uppercase"
              ml="12px"
            >{{ coin.key }}</Text>
          </CheckButton>
        </div>
      </div>
    </Container>
  </div>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import { useI18n } from '@/shared/plugins/i18n'
import Text from '@/shared/components/UIKit/Text/Text'
import Icon from '@/shared/components/Icon/Icon'
import Container from '@/shared/components/Containers/Container'
import coinsMock from '@/shared/store/mock/coinsMock'
import SelectTitle from '@/shared/components/Containers/SelectTitle'
import CheckButton from '@/admin/components/CheckButton'
import clickOutside from '@/shared/directives/clickOutside'

export default {
  name: 'SelectCoinPair',
  directives: {
    clickOutside
  },
  components: {
    CheckButton,
    Icon,
    Text,
    Container,
    SelectTitle
  },
  setup () {
    const { t } = useI18n()
    const isShowInput = ref(false)
    const dropdown = ref()

    const coins = reactive(coinsMock.map(item => ({
      ...item,
      checked: false
    })))
    const selected = reactive({
      any: [],
      from: [],
      to: []
    })
    const options = reactive({
      any: {
        label: 'labels.anyPairWith',
        disabled: false,
        checkDisable: () => {
          options.any.disabled = !!(selected.from.length || selected.to.length)
          return options.any.disabled
        }
      },
      from: {
        label: 'common.from',
        disabled: false,
        checkDisable: () => {
          options.from.disabled = !!selected.any.length
          return options.from.disabled
        }
      },
      to: {
        label: 'common.to',
        disabled: false,
        checkDisable: () => {
          options.to.disabled = !!selected.any.length
          return options.to.disabled
        }
      }
    })

    const isSelected = computed(() => {
      return !!(selected.any.length || selected.from.length || selected.to.length)
    })

    const hideInput = () => {
      isShowInput.value = false
    }
    const showInput = () => {
      isShowInput.value = true
    }
    const clearInput = () => {
      Object.keys(options).forEach(item => {
        selected[item] = []
        options[item].checkDisable()
      })
    }
    const findCoinIndex = (coin, type) => {
      return selected[type].findIndex(item => item.key === coin.key)
    }
    const isCoinSelected = (coin, type) => {
      return findCoinIndex(coin, type) !== -1
    }
    const selectCoin = (coin, type) => {
      if (options[type].checkDisable()) {
        return
      }
      const index = findCoinIndex(coin, type)
      if (index === -1) {
        selected[type].push(coin)
      } else {
        selected[type].splice(index, 1)
      }
      options.to.checkDisable()
      options.from.checkDisable()
      options.any.checkDisable()
    }
    return {
      t,
      coins,
      options,
      selected,
      dropdown,
      isSelected,
      isShowInput,
      showInput,
      hideInput,
      clearInput,
      selectCoin,
      isCoinSelected
    }
  }
}
</script>

<style lang="scss">
.select-coin-pair {
  position: relative;

  &__header {
    background-color: map-get($colors, "default");
    border-color: map-get($colors, "default");
  }
  &__body {
    position: absolute;
    right: 0;
    top: calc(100% + 10px);
    z-index: 20;
    display: flex;
    overflow: hidden;

    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
  }
  &__crypto-row {
    padding: 0 10px;

    &_disabled {
      cursor: not-allowed;

      .check-button {
        cursor: not-allowed;
        opacity: 0.3;
      }
    }
    .check-button {
      justify-content: unset;
      padding: 6px 20px;
      min-height: 40px;
      min-width: 129px;
      margin-bottom: 5px;
    }
  }
  &__from {
    padding: 20px 0 0 20px;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
  &__to {
    padding: 20px 20px 0 0;
  }
  &__any {
    padding: 20px 0;
    background-color: #FCFCFC;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}
</style>
