<template>
  <div class="select-sort d-flex align-items-center" @click="toggleSort">
    <Icon name="arrows-sort" opacity="0.4" />
    <Text size="14px" line-height="16px" ml="10px">
      {{ t('labels.' + value) }}
    </Text>
  </div>
</template>

<script>
import { useModel } from '@/shared/utils/useModel'
import Icon from '@/shared/components/Icon/Icon'
import Text from '@/shared/components/UIKit/Text/Text'
import { useI18n } from '@/shared/plugins/i18n'

export default {
  name: 'SelectSort',
  components: {
    Text,
    Icon
  },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      required: true
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const { value } = useModel(props, emit)

    const toggleSort = () => {
      value.value = value.value === 'newest' ? 'oldest' : 'newest'
    }

    return {
      t,
      value,
      toggleSort
    }
  }
}
</script>

<style lang="scss">
.select-sort {
  padding: 10px;
  cursor: pointer;
}
</style>
