<template>
  <div
    :class="{ 'check-button_active': value }"
    class="check-button"
    @click="toggle"
  >
    <slot />
  </div>
</template>

<script>
import { useModel } from '@/shared/utils/useModel'

export default {
  name: 'CheckButton',
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    const toggle = () => {
      value.value = !value.value
    }

    return {
      value,
      toggle
    }
  }
}
</script>

<style lang="scss">
.check-button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid transparent;
  cursor: pointer;

  &_active {
    background-color: #FBF3EC;
    border-color: map-get($colors, "primary");
  }
}
</style>
