<template>
  <Dropdown class="select-show-on-page">
    <template #label>
      <span class="select-show-on-page__label">
        {{ t('table.showOnPage') }}:
        <Text color="black" ml="8px">{{ perPage }}</Text>
      </span>
    </template>
    <template #body>
      <Container bordered box-shadow background-color="white" class="select-show-on-page__body">
        <div
          v-for="item in pageSettings"
          :key="item"
          class="select-show-on-page__item"
          @click="changeItemsPerPage(item)"
        >
          {{ item }}
        </div>
      </Container>
    </template>
  </Dropdown>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { reactive } from 'vue'
import { useModel } from '@/shared/utils/useModel'
import Text from '@/shared/components/UIKit/Text/Text'
import Dropdown from '@/shared/components/UIKit/Dropdown'
import Container from '@/shared/components/Containers/Container'

export default {
  name: 'SelectShowOnPage',
  components: {
    Text,
    Dropdown,
    Container
  },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      required: true
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const { value: perPage } = useModel(props, emit)

    const pageSettings = reactive([100, 200, 500])

    const changeItemsPerPage = (item) => {
      perPage.value = item
    }
    return {
      t,
      perPage,
      pageSettings,
      changeItemsPerPage
    }
  }
}
</script>

