  &__label {
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 16px;
    color: #A2A1A1;

    &-icon {
      margin: 0 0 2px 8px;
    }
  }
  &__body {
    min-width: 88px;
    border-radius: 10px;
  }
  &__item {
    padding: 10px 20px;
    cursor: pointer;

    &:hover {
      background-color: $selectedBackground;
    }
  }
}
</style>
