<template>
  <section class="swaps-grid-table">
    <Table
      :data="data"
      :columns="columns"
      class="swaps-grid-table__layout"
      @row-click="onRowClick"
    >
      <template #column="{ column }">
        <span class="swaps-grid-table__column">{{ t('swaps.table.columns.' + column.name) }}</span>
      </template>
      <template #cell="{ item, column, row }">
        <CellFormatter
          :column="column"
          :item="item"
          :row="row"
        />
      </template>
    </Table>
    <Pagination
      v-model="page"
      :per-page="perPage"
      :total-count="totalCount"
      class="swaps-grid-table__pagination"
    />
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed } from 'vue'
import { useRoute, useRouter } from '@/shared/plugins/vue-router'
import { useMutations, useGetters } from '@/shared/plugins/vuex'
import { ROUTES_NAMES } from '@/admin/common/routes'
import { MODULES_NAMES } from '@/shared/common/modules'
import Table from '@/admin/components/Table/Table'
import Pagination from '@/shared/components/UIKit/Pagination'
import CellFormatter from '@/admin/components/Table/CellFormatter/CellFormatter'

export default {
  name: 'SwapsGridTable',
  components: {
    CellFormatter,
    Pagination,
    Table
  },
  setup () {
    const { t } = useI18n()
    const route = useRoute()
    const router = useRouter()
    const {
      getSwaps: data,
      getActiveColumns: columns,
      getPerPage: perPage,
      getTotalCount: totalCount,
      getPage
    } = useGetters(MODULES_NAMES.SWAPS)
    const {
      SET_PAGE
    } = useMutations(MODULES_NAMES.SWAPS)

    const page = computed({
      get: () => getPage.value,
      set: SET_PAGE
    })
    console.log(route)
    const onRowClick = (row) => {
      router.push({
        name: ROUTES_NAMES.SWAPS_ITEM,
        params: {
          id: row.id
        }
      })
    }

    return {
      t,
      data,
      page,
      perPage,
      columns,
      totalCount,
      onRowClick
    }
  }
}
</script>

