<template>
  <PinCheck
    v-if="column.type === TABLE_TYPES.PIN"
    v-model="rowModel[column.name]"
  />
  <Badge
    v-else-if="column.type === TABLE_TYPES.STATUS"
    :type="COLORS[item]"
  >
    <Text v-if="STATUS[item]" color="inherit" weight="400">{{ t(STATUS[item]) }}</Text>
  </Badge>
  <Date
    v-else-if="column.type === TABLE_TYPES.DATE"
    format="DD MMM YYYY"
    :date="item"
  />
  <Date
    v-else-if="column.type === TABLE_TYPES.TIME"
    format="HH:mm A"
    :date="item"
  />
  <Text type="uppercase" v-else-if="column.type === TABLE_TYPES.CRYPTO_PAIR">
    {{ (item || [])[0] }} > {{ (item || [])[1] }}
  </Text>
  <Text type="uppercase" v-else-if="column.type === TABLE_TYPES.CURRENCY">
    {{ item }}
  </Text>
  <CellActions
    v-else-if="column.type === TABLE_TYPES.ACTIONS"
    :row="row"
    :item="item"
    :column="column"
  />
  <CellOptions
    v-else-if="column.type === TABLE_TYPES.OPTIONS"
    :row="row"
    :item="item"
    :column="column"
  />
  <span v-else>{{ item }}</span>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useModel } from '@/shared/utils/useModel'
import { defineAsyncComponent } from 'vue'
import { TABLE_TYPES } from '@/admin/common/tableTypes'
import { COLORS, STATUS } from '@/shared/common/status'

export default {
  name: 'CellFormatter',
  components: {
    Date: defineAsyncComponent(() => import('@/shared/components/UIKit/Date/Date')),
    Text: defineAsyncComponent(() => import('@/shared/components/UIKit/Text/Text')),
    Badge: defineAsyncComponent(() => import('@/shared/components/UIKit/Badge')),
    PinCheck: defineAsyncComponent(() => import('@/admin/components/Table/CellFormatter/PinCheck')),
    CellActions: defineAsyncComponent(() => import('@/admin/components/Table/CellFormatter/CellActions')),
    CellOptions: defineAsyncComponent(() => import('@/admin/components/Table/CellFormatter/CellOptions'))
  },
  props: {
    column: {
      default: () => ({})
    },
    row: {
      default: () => ({})
    },
    item: {
      default: ''
    }
  },
  setup (props, { emit }) {
    const { value: rowModel } = useModel(props, emit, { prop: 'row' })
    const { t } = useI18n()

    return {
      t,
      COLORS,
      STATUS,
      rowModel,
      TABLE_TYPES
    }
  }
}
</script>

